import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HomePage} from './home.page';
import {RegisterComponent} from "../../register/register.component";
import {SettingsComponent} from "../screens-admin/settings/settings.component";

const routes: Routes = [
  {
    path: '',
    component: HomePage,
    children: [
      {
        path: 'menu',
        loadChildren: () =>
          import('../screens-admin/listing-admin/listing-admin.module').then(
            (m) => m.ListingAdminPageModule
          ),
      },
      {
        path: 'add',
        loadChildren: () =>
          import('../screens-admin/add-food/add-food.module').then((m) => m.AddFoodModule),
      },

      {
        path: 'edit/:id',
        loadChildren: () =>
          import('../screens-admin/edit-food/edit-food.module').then((m) => m.EditFoodModule),

      },

      {
        path: 'add-sub-food/:id',
        loadChildren: () =>
          import('../screens-admin/add-sub-food/add-sub-food.module').then((m) => m.AddSubFoodModule),
      },

      {
        path: 'edit-sub-food/:id',
        loadChildren: () =>
          import('../screens-admin/edit-sub-food/edit-sub-food.module').then((m) => m.EditSubFoodModule),
      },
      {
        path: 'list-sub-food/:id',
        loadChildren: () =>
          import('../screens-admin/list-sub-food/list-sub-food.module').then((m) => m.ListSubFoodModule),
      },

      {
        path: 'add-category',
        loadChildren: () =>
          import('../../admin/components-admin/add-category/add-category.module').then((m) => m.AddCategoryModule),
      },
      {
        path: 'test',
        loadChildren: () =>
          import('../screens-admin/test/test.module').then((m) => m.TestModule),
      },
      {
        path: 'test2',
        loadChildren: () =>
          import('../screens-admin/test2/test.module').then((m) => m.Test2Module),
      },
      {
        path: 'revenus',
        loadChildren: () =>
          import('../screens-admin/revenus/revenus.module').then((m) => m.RevenusModule),
      },
      {
        path: 'commands/details/:id',
        loadChildren: () =>
          import('../screens-admin/detailscommand/detailscommand.module').then((m) => m.DetailscommandModule),
      },
      {
        path: 'profil',
        loadChildren: () =>
          import('../screens-admin/profil/profil.module').then((m) => m.ProfilModule),
      },
      {
        path: 'contact',
        loadChildren: () =>
          import('../screens-admin/contact/contact.module').then((m) => m.ContactModule),
      },
      {
        path: 'settings',
        loadChildren: () =>
          import('../screens-admin/settings/settings.module').then((m) => m.SettingsModule),
      },

      {
        path: '**',
        redirectTo: '/admin/menu',
        pathMatch: 'full'
      },
    ],
  },
  /*{
    path: '',
    redirectTo: '/login',
    pathMatch: 'full'
  }*/
];

// @ts-ignore
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HomePageRoutingModule {
}
